import React from 'react'
import Radium from 'radium'
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import Box from './elements/Box'
import {FlatButton, IconButton, TextField, Dialog, SelectField,MenuItem, RaisedButton, Table, TableRow, TableRowColumn,TableHeaderColumn, TableHeader, TableBody} from 'material-ui'
import * as colors from 'material-ui/styles/colors'
import Edit from 'material-ui/svg-icons/image/edit'
import Add from 'material-ui/svg-icons/content/add'
import actions from '../../actions/front'
import ShopCar from './elements/ShopCar'
import Alert from 'react-s-alert'
import _ from 'lodash'
const styles = {
	container: {
		marginTop: 30,
		display: 'flex',
		flexDirection: 'column',
		justifyContent: 'center',
		alignItems: 'center'
	},
	box: {
		width: '80%',
		minWidth: '800px'
	},
	addressContainer: {
		display: 'flex',
		flexDirection: 'row',
		justifyContent: 'space-around',
		alignItems:'center',
		padding: '1em',
	},
	address: {
		width: '30%',
		padding: '0.8em',
		height: 'auto',
		lineHeight: 'auto',
		borderRadius: 5,
		position: 'relative',
		textAlign: 'left',
		border: `2px solid ${colors.orangeA200}`,
	},
	addressNormal: {
		width: '30%',
		padding: '0.8em',
		height: 'auto',
		lineHeight: 'auto',
		borderRadius: 5,
		position: 'relative',
		textAlign: 'left',
		border: `2px solid ${colors.grey300}`,
	},
	addressSelect: {
		position: 'absolute',
		top: 10,
		right: 10,
		lineHeight: '16px',
		width: 16,
		height: 16,
		borderRadius: 8,
		border: 0,
		color: colors.white,
		textAlign: 'center',
		background: colors.orangeA200

	},
	ellipsis: {
		margin: 0,
		// textOverflow: 'ellipsis', /**/
		// overflow: 'hidden',/**/
		// whiteSpace: 'nowrap',/**/
		width: '100%'/**/
	},
	invoiceContainer: {
		display: 'flex',
		flexDirection: 'column',
		justifyContent: 'flex-start',
		alignItems:'center',
		padding: '1em',
	},
	dialogContainer: {
		width: '50%',
		maxWidth: 'none',
	},

}

class Buy extends React.Component {

	state = {
		invoiceEditOpen: false, // 
		addressAddOpen: false, // 
		address: {}, // 
		invoices: {}, // 
		addresses: [], // 
		sheng: [],
		di: [],
		xian: [],
		selectedSheng: '',
		selectedDi: '',
		selectedXian: '',
		selectedReceiveAddress: '', // 
	}

	componentWillMount() {
		// 
		this._getShengList()
	}

	/**
	 * 
	 * @param id
	 */
	handleReceiveAddressSelect = (id) => {
		this.setState({
			selectedReceiveAddress: id
		})
	}

	handleInvoiceClose = () => {
		this.setState({
			invoiceEditOpen: false
		})
	}

	handleInvoiceOpen = () => {
		this.setState({
			invoiceEditOpen: true
		})
	}

	invoicesSet = (key, e) => {
		this.setState({
			invoices: {
				...this.state.invoices,
				[key]: e.target.value
			}
		})
	}

	handleAddressAddOpen = () => {
		this.setState({
			addressAddOpen: true
		})
	}

	handleAddressAddClose = () => {
		this.setState({
			addressAddOpen: false
		})
	}

	addressSet = (key, e) => {
		this.setState({
			address: {
				...this.state.address,
				[key]: e.target.value
			}
		})
	}

	_handleAddressAddClick = () => {

		let {detailAddress, name, phone} = this.state.address
		let {selectedSheng, selectedDi, selectedXian} = this.state
		if (!name|| !phone || !selectedSheng || !selectedDi || !selectedXian || !detailAddress) {
			Alert.info("")
			return
		}
		this.handleAddressAddClose()
		let address = `${selectedSheng}${selectedDi}${selectedXian}${detailAddress}`
		this.props.actions.addOrUpdateReceiveAddress({name, phone, address,
			token: this.props.user.token || localStorage.getItem('token')
		}).then((res) => {
			if (this.props.user.token || localStorage.getItem('token')) {
				this.props.actions.loginWithToken(this.props.user.token || localStorage.getItem('token'))
			}
		}).catch((err) => {
			err.res.then((value) => {
				Alert.error(value.message)
			})
		})
	}
	/**
	 * select
	 * @param key
	 * @param event
	 * @param index
	 * @param value
	 * @private
	 */
	_handleAddressChange = (key, event, index, value) => {

		// 
		if (key === 'selectedSheng'){
			this.setState({
				xian: []
			})
		}
		this.setState({
			[key]: value
		})
	}

	/**
	 * 
	 * @private
	 */
	_getShengList = () => {
		const {getCityList} = this.props.actions
		getCityList({level: 1}).then((res) => {
			this.setState({
				sheng: _.map(res.value,(item) => {
					return (
						<MenuItem key={item.sheng} value={item.name} primaryText={item.name} onTouchTap={this._getDiList.bind(this, item.sheng)}/>
					)
				})
			})
		}).catch((err) => {
			console.log(err)
		})

	}

	/**
	 * 
	 * @param sheng
	 * @private
	 */
	_getDiList = (sheng) => {
		const {getCityList} = this.props.actions
		getCityList({level: 2, sheng}).then((res) => {
			this.setState({
				di: _.map(res.value, (item) => {
					return (
						<MenuItem key={item.di} value={item.name} primaryText={item.name} onTouchTap={this._getXianList.bind(this, item.sheng, item.di)}/>
					)
				})
			})
		}).catch((err) => {
			console.log(err)
		})
	}

	/**
	 * 
	 * @param sheng
	 * @param di
	 * @private
	 */
	_getXianList = (sheng, di) => {
		const {getCityList} = this.props.actions
		getCityList({level: 3, sheng, di}).then((res) => {
			this.setState({
				xian: _.map(res.value, (item) => {
					return (
						<MenuItem key={item.xian} value={item.name} primaryText={item.name}/>
					)
				})
			})
		}).catch((err) => {
			console.log(err)
		})
	}

	/**
	 * 
	 * @returns {XML}
	 * @private
	 */
	_renderReceiveAddress = () => {
		let receiveAddress = (this.props.user && this.props.user.info && this.props.user.info.receiveAddress) || []
		return (
			<div style={styles.addressContainer}>
				{
					!!receiveAddress && receiveAddress.length > 0 ? (!this.state.selectedReceiveAddress && receiveAddress.length > 0 && this.setState({selectedReceiveAddress: receiveAddress[0]._id})) || _.map(receiveAddress, (item) => {
						return (
							<FlatButton
								style={this.state.selectedReceiveAddress === item._id ? styles.address : styles.addressNormal}
								key={item._id}
								onTouchTap={this.handleReceiveAddressSelect.bind(this, item._id)}
								icon={
									<div>
										{this.state.selectedReceiveAddress === item._id && <p style={styles.addressSelect}></p>}
										<p style={styles.ellipsis}>{item.name}</p>
										<p style={styles.ellipsis}>{item.phone}</p>
										<p style={styles.ellipsis}>{item.address}</p>
									</div>
								}/>
							)
						}) : <p style={{color: colors.red400}}></p>
					}

			</div>
		)
	}

	render(){

		const invoiceActions = [
			<FlatButton
				label=""
				onTouchTap={this.handleInvoiceClose}
			/>,
			<FlatButton
				label=""
				primary={true}
				keyboardFocused={true}
				onTouchTap={this.handleInvoiceClose}
			/>
		]

		const addressActions = [
			<FlatButton
				label=""
				secondary={true}
				onTouchTap={this.handleAddressAddClose}
			/>,
			<FlatButton
				label=""
				primary={true}
				keyboardFocused={true}
				onTouchTap={this._handleAddressAddClick}
			/>
		]

		return (

			<div style={styles.container}>
				<Box title=""
				     style={styles.box}
				     menu={<IconButton tooltip="" onTouchTap={this.handleAddressAddOpen}><Add color={colors.grey700}/></IconButton>}>
					<Dialog
						open={this.state.addressAddOpen}
						title=""
						actions={addressActions}
						modal={false}
						onRequestClose={this.handleAddressAddClose}
						contentStyle={styles.dialogContainer}
					>
						<TextField
							onChange={this.addressSet.bind(this, 'name')}
							fullWidth={true}
							floatingLabelText=""
							hintText={``}/>
						<TextField
							onChange={this.addressSet.bind(this, 'phone')}
							fullWidth={true}
							floatingLabelText=""
							hintText={``}/>

						<SelectField
							style={{width: '100%'}}
							floatingLabelText=""
							floatingLabelFixed={true}
							hintText=""
							value={this.state.selectedSheng}
							onChange={this._handleAddressChange.bind(this, 'selectedSheng')}
						>
							{this.state.sheng}
						</SelectField>
						<SelectField
							style={{width: '100%'}}
							floatingLabelText=""
							value={this.state.selectedDi}
							onChange={this._handleAddressChange.bind(this, 'selectedDi')}
						>
							{this.state.di}
						</SelectField>
						<SelectField
							style={{width: '100%'}}
							floatingLabelText=""
							value={this.state.selectedXian}
							onChange={this._handleAddressChange.bind(this, 'selectedXian')}
						>
							{this.state.xian}
						</SelectField>
						<TextField
							onChange={this.addressSet.bind(this, 'detailAddress')}
							fullWidth={true}
							floatingLabelText=""
							hintText=""/>
					</Dialog>
					{this._renderReceiveAddress()}

				</Box>
				<Box title={""}
				     style={[styles.box, {marginTop: 20}]}
				     menu={
				     	<IconButton tooltip="" onClick={this.handleInvoiceOpen}>
				     	  <Edit color={colors.grey700} />
				     	</IconButton>}
				>
					<Dialog
						open={this.state.invoiceEditOpen}
						title=""
						actions={invoiceActions}
						modal={false}
						onRequestClose={this.handleInvoiceClose}
						contentStyle={styles.dialogContainer}
					>
						{['', '', '', '', '', '', '', ''].map((item,index) => {
							return (
								<TextField
									value={this.state.invoices[item]}
									type={(index === 2 || index === 4) ? 'number' : 'text'}
									onChange={this.invoicesSet.bind(this, item)}
									key={index}
									fullWidth={true}
									floatingLabelText={item}
									hintText={`${item}`}/>
							)
						})}
					</Dialog>
					<div style={styles.invoiceContainer}>
						<p style={styles.ellipsis}>: {this.state.invoices[''] || ''}</p>
						<p style={styles.ellipsis}>: {this.state.invoices[''] || (!!this.props.user.info && (this.props.user.info.username || this.props.user.info.companyName)) || ''}</p>
						<p style={styles.ellipsis}>: {this.state.invoices[''] || ''}</p>
						<p style={styles.ellipsis}>: {this.state.invoices[''] || 'xxxxxxxx'}</p>
						<p style={styles.ellipsis}>: {this.state.invoices[''] || (!!this.props.user.info && this.props.user.info.address) || 'xxxxxxxx'}</p>
						<p style={styles.ellipsis}>: {this.state.invoices[''] || (!!this.props.user.info && this.props.user.info.fixedPhone || (!!this.props.user.info && !!this.props.user.info.receiveAddress && !!this.props.user.info.receiveAddress[0] && this.props.user.info.receiveAddress[0].phone)) || 'xxxxxxxxxxx'}</p>
						<p style={styles.ellipsis}>: {this.state.invoices[''] || 'xx'}</p>
						<p style={styles.ellipsis}>: {this.state.invoices[''] || '62xxxxxxxxxxxxxxx'}</p>

						<br/>
						<p style={styles.ellipsis}>,</p>
					</div>
				</Box>
				<Box title="" style={[styles.box, {marginTop: 20}]}>
					<ShopCar orderInfo={{
						address: !!this.state.selectedReceiveAddress && _.find(!!this.props.user && !!this.props.user.info && this.props.user.info.receiveAddress || [], (item) => {
							return item._id === this.state.selectedReceiveAddress
						}),
						invoice: {
							'': '',
							'': (!!this.props.user.info && (this.props.user.info.username || this.props.user.info.companyName)) || '',
							'': '',
							'': !!this.props.user.info && this.props.user.info.address,
							'': (!!this.props.user.info && this.props.user.info.fixedPhone || (!!this.props.user.info && !!this.props.user.info.receiveAddress && !!this.props.user.info.receiveAddress[0] && this.props.user.info.receiveAddress[0].phone)),
							...this.state.invoices
						}
					}}/>
				</Box>
			</div>
		)



	}
}

function mapStateToProps(state) {
	/* Populated by react-webpack-redux:reducer */
	const props = {
		user: state.user
	};
	return props;
}
function mapDispatchToProps(dispatch) {
	/* Populated by react-webpack-redux:action */
	return {
		actions: {...bindActionCreators(actions, dispatch)}
	}
}



export default connect(mapStateToProps, mapDispatchToProps)(Radium(Buy))


